function numberFrom(str: string) {
  const pr = ['normal', 'high', 'urgent'];
  const idx = pr.indexOf(str);
  return idx !== -1 ? idx : 0;
}

function sortTasksByPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
      return numberFrom(b.priority) - numberFrom(a.priority);
    });
  }
  return true;
}
function sortTasksByIdOfPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (a.priority === b.priority) {
        if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
        return numberFrom(b.priority) - numberFrom(a.priority);
      }
      return 0;
    });
  }
  return true;
}

function getCategoryIndex(catList: Categoryes[], id: number): number | null {
  const idx = catList.findIndex((cat) => cat.id === id);
  return idx !== -1 ? idx : null;
}

const reducer = function (state: StateInit = initState, action: Actions) {
  const newState = { ...state };
  switch (action.type) {
    case 'ADD_CATEGORY':
      {
        const valuesArr = action.payload as Values;
        const title: string = valuesArr.inputValue;
        const newCategory = new CategoryItem(title, state.lastCatId);
        newState.categoryes.push(newCategory);
        newState.currentCategoryId = newCategory.id;
        newState.lastCatId = newCategory.id;
        newState.isVisibleModal = true;
      }
      break;
    case 'SELECT_CATEGORY':
      {
        if (typeof action.payload === 'number')
          newState.currentCategoryId = action.payload;
      }
      break;
    case 'DELETE_CATEGORY_ITEM':
      {
        const idx = newState.categoryes.findIndex(
          (i) => i.id === action.payload
        );
        newState.categoryes.splice(idx, 1);
        newState.currentCategoryId =
          newState.categoryes.length > 0 ? newState.categoryes[0].id : null;
      }
      break;
    case 'EDIT_CATEGORY_TITLE':
      {
        let [title, id] = action.payload as [string, number | null];
        const editIndex = newState.categoryes.findIndex((i) => i.id === id);
        if (editIndex !== -1) {
          newState.categoryes[editIndex].description = title;
        }
      }
      break;
    case 'REODER_CATEGORY':
      {
        const [source, dest] = action.payload as [number, number | null];
        if (dest !== null) {
          const item = newState.categoryes.splice(source, 1)[0];
          newState.categoryes.splice(dest, 0, item);
        }
      }
      break;
    case 'ADD_TASK_ITEM':
      {
        if (action.payload) {
          const values = action.payload as Values;
          const newTaskItem = new TaskItem(values, state.lastTaskId);
          const catID = newState.currentCategoryId;
          console.log(newTaskItem);
          const currentCategory = newState.categoryes.find(
            (c) => c.id === catID
          );
          if (currentCategory) {
            currentCategory.tasksList.push(newTaskItem);
            sortTasksByPriority(currentCategory.tasksList);
            sortTasksByIdOfPriority(currentCategory.tasksList);
          }
        }
      }
      break;
    case 'TASK_DONE_TOGGLE':
      {
        const taskId = action.payload as number;
        const catID = newState.currentCategoryId;
        if (catID !== null) {
          const curerentCategory = newState.categoryes.find(
            (c) => c.id === catID
          );
          if (curerentCategory) {
            let currentTask = curerentCategory.tasksList.find(
              (t) => t.id === taskId
            );
            if (currentTask) {
              console.log('toggle task:---', currentTask);
              const newTask = {
                ...currentTask,
                isCompleted: !currentTask.isCompleted,
              };
              const newList = [...curerentCategory.tasksList];
              const idx = newList.findIndex((t) => t.id === taskId);
              newList.splice(idx, 1, newTask);
              curerentCategory.tasksList = newList;
            }
          }
        }
      }
      break;
    case 'REMOVE_MODAL':
      {
        newState.isVisibleModal = false;
      }
      break;
    case 'SHOW_MODAL': {
      newState.isVisibleModal = true;
    }
  }
  console.log('cat id:', newState);
  return newState;
};

export { reducer };
